<template>
    <div class="pos">
        <!-- <leftNav></leftNav> -->
        <el-row>
            <el-col :span='7' class="pos-order" id="order-list">
                <el-tabs>
                    <el-tab-pane label="点餐">
                        <el-table :data="tableData" border style="width:100%">
                            <el-table-column prop="goodsName" label="商品名称" ></el-table-column>
                            <el-table-column prop="count" label="数量"></el-table-column>
                            <el-table-column prop="price" label="金额"></el-table-column>
                            <el-table-column label="操作" fixed="right">
                                <template slot-scope="scope">
                                <el-button type="text" size="small" @click="deletSingle(scope.row)">删除</el-button>
                                <el-button type="text" size="small" @click="addOrderList(scope.row)">增加</el-button>
                                </template>
                            </el-table-column>
                        </el-table>
                        <div class="total">
                            <small>数量:</small><b>{{totalCount}}</b>
                            <small>总计:</small><b>{{totalMoney}}元</b>
                        </div>
                        <div class="btns">
                            <el-button type="warning">挂单</el-button>
                            <el-button type="danger" @click="deletAll">取消</el-button>
                            <el-button type="success" @click="checkOut">结账</el-button>
                        </div>
                    </el-tab-pane>
                    <el-tab-pane label="挂单">
                        挂单
                    </el-tab-pane>
                    <el-tab-pane label="外卖">
                        外卖
                    </el-tab-pane>
                </el-tabs>
            </el-col>
            <el-col :span="17">
                <div class="often-goods">
                    <div class="title">热销商品</div>
                    <div class="often-goods-list">
                        <ul>
                            <li v-for="(goods,i) of oftenGoods" :key="i" @click="addOrderList(goods)">
                                <span>{{goods.goodsName}}</span>
                                <span class="o-price">¥{{goods.price}}元</span>
                            </li>
                        </ul>
                    </div>
                </div>

                <div class="goodsType">
                    <el-tabs>
                        <el-tab-pane label="汉堡">
                            <div>
                                <ul class='cookList'>
                                    <li v-for="(goods,i) of type0Goods" :key="i" @click="addOrderList(goods)">
                                        <span class="foodImg">
                                            <img :src="goods.goodsImg" width="100%">
                                        </span>
                                        <span class="foodName">{{goods.goodsName}}</span>
                                        <span class="foodPrice">￥{{goods.price}}元</span>
                                    </li>
                                </ul>
                            </div>
                        </el-tab-pane>
                        <el-tab-pane label="小食">
                            <div>
                                <ul class='cookList'>
                                    <li v-for="(goods,i) of type1Goods" :key="i" @click="addOrderList(goods)">
                                        <span class="foodImg"><img :src="goods.goodsImg" width="100%"></span>
                                        <span class="foodName">{{goods.goodsName}}</span>
                                        <span class="foodPrice">￥{{goods.price}}元</span>
                                    </li>
                                </ul>
                            </div>
                        </el-tab-pane>
                        <el-tab-pane label="饮料">
                            <div>
                                <ul class='cookList'>
                                    <li v-for="(goods,i) of type2Goods" :key="i" @click="addOrderList(goods)">
                                        <span class="foodImg"><img :src="goods.goodsImg" width="100%"></span>
                                        <span class="foodName">{{goods.goodsName}}</span>
                                        <span class="foodPrice">￥{{goods.price}}元</span>
                                    </li>
                                </ul>
                            </div>
                        </el-tab-pane>
                        <el-tab-pane label="套餐">
                            <div>
                                <ul class='cookList'>
                                    <li v-for="(goods,i) of type3Goods" :key="i">
                                        <span class="foodImg"><img :src="goods.goodsImg" width="100%"></span>
                                        <span class="foodName">{{goods.goodsName}}</span>
                                        <span class="foodPrice">￥{{goods.price}}元</span>
                                    </li>
                                </ul>
                            </div>
                        </el-tab-pane>
                    </el-tabs>
                </div>

            </el-col>
        </el-row>
    </div>
</template>

<style scoped>
.pos-order{
    background-color: #f9fafc;
    border-right: 1px solid #c0ccda;
}
.btns{
    margin-top: 10px;
}
.title{
    height: 20px;
    border-bottom: 1px solid #D3dce6;
    background-color: #F9FAFC;
    padding: 9px;
    text-align: left;
}
.often-goods-list ul li{
    list-style: none;
    float: left;
    border: 1px solid #0aa1ed;
    padding: 10px;
    margin: 10px;
    background-color: #fff;
    cursor: pointer;
}
.o-price{
    color: #58b7ff;
    margin-left: 8px;
}
.goodsType{
    clear: both;
}
.cookList li{
       list-style: none;
       width:23%;
       border:1px solid #E5E9F2;
       height: auot;
       overflow: hidden;
       background-color:#fff;
       padding: 2px;
       float:left;
       margin: 2px;
       cursor: pointer;
   }
   .cookList li span{

        display: block;
        float:left;
   }
   .foodImg{
       width: 40%;
       height: 60px;
   }
   .foodImg>img{
       height: 100%;
   }
   .foodName{
       font-size: 18px;
       padding-left: 10px;
       color:brown;

   }
   .foodPrice{
       font-size: 16px;
       padding-left: 10px;
       padding-top:10px;
   }
   .total{
       background-color: #fff;
       padding: 10px;
       border-bottom: 1px solid #E5E9F2;
       display: flex;
       justify-content: space-around;
   }
</style>

<script>
import leftNav from '../components/leftNav'
import axios from 'axios'
export default {
    name:"Pos",
    data(){
        return{
        tableData: [ ],
        oftenGoods:[ ],
        type0Goods:[ ],
        type1Goods:[ ],
        type2Goods:[ ],
        type3Goods:[ ],
        
        }
    },
    computed: {
        totalCount() {
            return this.tableData.reduce((pre, goods) => {
            return pre + goods.count;
            } , 0)
        },
        totalMoney() {
            return this.tableData.reduce((pre, goods) => {
            return pre + goods.count * goods.price;
            }, 0)
        }
    },
    methods:{
        addOrderList(goods){
            // this.totalMoney=0;
            // this.totalCount=0;

            let isHave=false;
            for(let i=0;i<this.tableData.length;i++){
                if(this.tableData[i].goodsId==goods.goodsId)
                isHave=true
            };
            if(isHave){
                //存在就进行数量添加
                 let arr = this.tableData.filter(oldOrder =>oldOrder.goodsId == goods.goodsId);
                 arr[0].count++;
                 //console.log(arr);
            }else{
                //不存在就推入数组
                let newGoods={goodsId:goods.goodsId,goodsName:goods.goodsName,price:goods.price,count:1};
                 this.tableData.push(newGoods);
            }
            // this.tableData.forEach(element=>{
            //     this.totalCount+=element.count;
            //     this.totalMoney=this.totalMoney+(element.price*element.count);
            // })
        },
        deletSingle(goods){
            this.tableData=this.tableData.filter(oldOrder=>oldOrder.goodsName != goods.goodsName);
        },
        deletAll(){
            this.tableData=[];
            this.totalMoney=0;
            this.totalCount=0;
        },
        checkOut(){
            if(this.totalCount!=0){
                this.tableData=[];
                this.totalMoney=0;
                this.totalCount=0;
                this.$message({
                    message:"结账成功!谢谢!",
                    type:"success"
                })
            }else{
                this.$message.error("没有订单,结账失败!")
            }
        }
    },
    created(){
        let url1='https://www.fastmock.site/mock/0bf6a5bae7eab8507e44b56191ddff36/vuepos/oftenGoods';
        axios.get(url1).then((res)=>{
            
            this.oftenGoods=res.data.oftenGoods
        }).catch(err=>{res.send({msg:"数据读取错误"})})
        let url2='https://www.fastmock.site/mock/0bf6a5bae7eab8507e44b56191ddff36/vuepos/typeGoods';
        axios.get(url2).then(res=>{ 
            this.type0Goods=res.data.data[0]
            this.type1Goods=res.data.data[1]
            this.type2Goods=res.data.data[2]
            this.type3Goods=res.data.data[3]  
        }).catch(err=>{res.send({msg:"数据读取错误"})})
    },
    components:{leftNav},
    mounted(){
        var orderHeight=document.body.clientHeight;
        document.getElementById('order-list').style.height=orderHeight+'px'
    }
}
</script>